"use client";

import React, { useState, useRef, useEffect } from 'react';
import DashboardLayout from '@/components/DashboardLayout';
import { Card } from '@/components/Card';
import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Calendar, Clock, Video, MessageSquare, Send, Play, Pause, RotateCw } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Input } from '@/components/ui/input';

interface TranscriptItem {
  timestamp: number; // in seconds
  text: string;
}

interface Diagnostic {
  id: number;
  date: string;
  time: string;
  type: string;
  status: 'completed' | 'pending' | 'in-progress';
  videoUrl?: string;
  summary: string;
  transcript?: TranscriptItem[];
}

interface Message {
  id: number;
  text: string;
  sender: 'user' | 'bot';
  timestamp: string;
}

export default function DiagnosticsPage() {
  const [selectedDiagnostic, setSelectedDiagnostic] = useState<Diagnostic | null>(null);
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputMessage, setInputMessage] = useState('');
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const videoRef = useRef<HTMLVideoElement>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  // Sample diagnostic history data with transcripts
  const diagnosticHistory: Diagnostic[] = [
    {
      id: 1,
      date: 'November 8, 2024',
      time: '10:30 AM',
      type: 'Cognitive Assessment Instructions',
      status: 'completed',
      videoUrl: '/sample-video-placeholder.html', // Using placeholder due to YouTube blocking
      summary: 'Instructions for daily cognitive exercises and memory enhancement techniques.',
      transcript: [
        { timestamp: 0, text: "Hello, I'm Dr. Smith. Today I'll explain your cognitive exercise routine." },
        { timestamp: 5, text: "These exercises are designed to maintain and improve your memory function." },
        { timestamp: 12, text: "First, let's discuss the importance of daily mental stimulation." },
        { timestamp: 18, text: "Start each morning with a simple word puzzle or crossword." },
        { timestamp: 25, text: "This helps activate different areas of your brain." },
        { timestamp: 32, text: "Next, practice the memory palace technique I'm demonstrating." },
        { timestamp: 39, text: "Associate items you need to remember with familiar locations." },
        { timestamp: 46, text: "For medication, use a pillbox with day labels - very important." },
        { timestamp: 53, text: "Set phone alarms for your three daily medication times: 8am, 2pm, and 8pm." },
        { timestamp: 61, text: "Keep a daily journal to track your thoughts and activities." },
        { timestamp: 68, text: "Writing helps reinforce memory and provides structure to your day." },
        { timestamp: 75, text: "Practice naming objects around you - this maintains word retrieval." },
        { timestamp: 82, text: "Do the finger exercises I showed you - they help with coordination." },
        { timestamp: 89, text: "Stay socially active - call a friend or family member daily." },
        { timestamp: 96, text: "Physical exercise is crucial - aim for 30 minutes of walking." },
        { timestamp: 103, text: "If you feel confused, use your emergency card with key information." },
        { timestamp: 110, text: "Remember, consistency is key - do these exercises every day." },
        { timestamp: 117, text: "Your next assessment is in two weeks. Keep up the great work!" }
      ]
    },
    {
      id: 2,
      date: 'November 1, 2024',
      time: '2:15 PM',
      type: 'Memory Assessment Results',
      status: 'completed',
      videoUrl: '/memory-assessment-video', // Simulated video
      summary: 'Review of memory assessment results and personalized improvement strategies.',
      transcript: [
        { timestamp: 0, text: "Good afternoon, I'm Dr. Johnson reviewing your memory test results." },
        { timestamp: 6, text: "Your assessment shows strong performance in several key areas." },
        { timestamp: 13, text: "Short-term memory recall: You remembered 7 out of 10 items - very good." },
        { timestamp: 20, text: "For the word list, you recalled 85% after the delay period." },
        { timestamp: 27, text: "Visual memory showed excellent retention of shapes and patterns." },
        { timestamp: 34, text: "Now, let's discuss areas where we can work on improvement." },
        { timestamp: 41, text: "Sequential memory - remembering things in order - needs practice." },
        { timestamp: 48, text: "Try this technique: create a story linking items together." },
        { timestamp: 55, text: "For numbers, use the chunking method - group them in threes." },
        { timestamp: 62, text: "Your medication schedule: morning pills with breakfast at 8am." },
        { timestamp: 69, text: "Afternoon dose with lunch at 1pm - set a phone reminder." },
        { timestamp: 76, text: "Evening medication with dinner at 6pm - very important timing." },
        { timestamp: 83, text: "Use the memory notebook I'm providing to track daily tasks." },
        { timestamp: 90, text: "Write down three things you did each day before bed." },
        { timestamp: 97, text: "This reinforces memory consolidation during sleep." },
        { timestamp: 104, text: "Practice the 'look, snap, connect' technique for new faces." },
        { timestamp: 111, text: "Your results show no signs of significant decline - excellent news!" },
        { timestamp: 118, text: "Continue these exercises daily and we'll reassess in one month." }
      ]
    },
    {
      id: 3,
      date: 'October 25, 2024',
      time: '11:00 AM',
      type: 'Speech Therapy Instructions',
      status: 'completed',
      videoUrl: '/speech-therapy-video', // Simulated video
      summary: 'Personalized speech exercises to maintain articulation and fluency.',
      transcript: [
        { timestamp: 0, text: "Hello, I'm Sarah, your speech therapist. Let's review your exercises." },
        { timestamp: 7, text: "These exercises will help maintain clear speech and word finding." },
        { timestamp: 14, text: "First exercise: tongue stretches - stick your tongue out and hold." },
        { timestamp: 21, text: "Move it left, then right, holding each position for 5 seconds." },
        { timestamp: 28, text: "Now, lip exercises - purse your lips like whistling, then smile wide." },
        { timestamp: 35, text: "Repeat this 10 times each morning to strengthen facial muscles." },
        { timestamp: 42, text: "For voice strength: hum at different pitches for 30 seconds." },
        { timestamp: 49, text: "Practice saying 'pa-pa-pa' and 'ba-ba-ba' rapidly but clearly." },
        { timestamp: 56, text: "Word retrieval exercise: name 5 items in categories I'll give you." },
        { timestamp: 63, text: "Animals, foods, colors - take your time, accuracy over speed." },
        { timestamp: 70, text: "Read aloud for 10 minutes daily - newspapers work great." },
        { timestamp: 77, text: "If you struggle with a word, describe it instead of stopping." },
        { timestamp: 84, text: "Sing familiar songs - this helps with rhythm and breathing." },
        { timestamp: 91, text: "Practice conversation with family - don't avoid social situations." },
        { timestamp: 98, text: "Use gestures when speaking - they support communication." },
        { timestamp: 105, text: "Record yourself weekly to track progress - you're doing well!" },
        { timestamp: 112, text: "Remember: consistent practice is key to maintaining speech clarity." }
      ]
    },
    {
      id: 4,
      date: 'October 18, 2024',
      time: '3:45 PM',
      type: 'Daily Living Skills Review',
      status: 'completed',
      videoUrl: '/simulated-video-daily-living', // Simulated video
      summary: 'Strategies for maintaining independence in daily activities.',
      transcript: [
        { timestamp: 0, text: "Welcome, I'm Dr. Martinez. Let's review your daily living strategies." },
        { timestamp: 7, text: "These tips will help you maintain independence safely." },
        { timestamp: 14, text: "Morning routine: Keep items in the same place every day." },
        { timestamp: 21, text: "Use a checklist by your bed - mark off each completed task." },
        { timestamp: 28, text: "Medication management: Use your weekly pill organizer." },
        { timestamp: 35, text: "Take photos of your pills to remember what they look like." },
        { timestamp: 42, text: "Kitchen safety: Use timers for all cooking - never leave stove unattended." },
        { timestamp: 49, text: "Keep a fire extinguisher visible and know how to use it." },
        { timestamp: 56, text: "Label cabinets with pictures or words for easy finding." },
        { timestamp: 63, text: "Personal hygiene: Set out clothes the night before." },
        { timestamp: 70, text: "Use pump dispensers instead of bar soap - easier to manage." },
        { timestamp: 77, text: "Install grab bars in the shower for safety." },
        { timestamp: 84, text: "Important phone numbers: program them with photos in your phone." },
        { timestamp: 91, text: "Carry an ID card with emergency contact and medical info." },
        { timestamp: 98, text: "Use a calendar with large print - cross off each day." },
        { timestamp: 105, text: "These strategies promote independence while ensuring safety." }
      ]
    },
    {
      id: 5,
      date: 'October 10, 2024',
      time: '9:30 AM',
      type: 'Physical Exercise Guide',
      status: 'completed',
      videoUrl: '/simulated-video-exercise', // Simulated video
      summary: 'Safe physical exercises to improve balance and cognitive function.',
      transcript: [
        { timestamp: 0, text: "Good morning, I'm Tom, your physical therapist." },
        { timestamp: 6, text: "Today's exercises improve both physical and cognitive health." },
        { timestamp: 13, text: "Always start with gentle stretching - never force movements." },
        { timestamp: 20, text: "Chair exercises first: sit tall, march your feet in place." },
        { timestamp: 27, text: "Lift your knees high, continue for 30 seconds." },
        { timestamp: 34, text: "Arm circles: extend arms to sides, make small circles." },
        { timestamp: 41, text: "10 forward, then 10 backward - this helps coordination." },
        { timestamp: 48, text: "Standing balance: hold a chair, lift one foot for 10 seconds." },
        { timestamp: 55, text: "Switch feet - if you feel unsteady, keep both hands on chair." },
        { timestamp: 62, text: "Walking exercise: walk heel to toe in a straight line." },
        { timestamp: 69, text: "This improves balance and reduces fall risk significantly." },
        { timestamp: 76, text: "Brain-body exercise: count backward from 20 while marching." },
        { timestamp: 83, text: "This dual-task training is excellent for cognitive function." },
        { timestamp: 90, text: "Cool down: deep breathing, inhale for 4, hold for 4, exhale for 4." },
        { timestamp: 97, text: "Do these exercises daily, preferably at the same time." },
        { timestamp: 104, text: "Stop if you feel dizzy or short of breath - safety first!" }
      ]
    },
    {
      id: 6,
      date: 'October 3, 2024',
      time: '1:00 PM',
      type: 'Nutrition Guidelines',
      status: 'completed',
      videoUrl: '/simulated-video-nutrition', // Simulated video
      summary: 'Dietary recommendations for optimal brain health and medication effectiveness.',
      transcript: [
        { timestamp: 0, text: "Hello, I'm Lisa, your nutritionist. Let's discuss your brain-healthy diet." },
        { timestamp: 7, text: "Proper nutrition is crucial for cognitive health and medication absorption." },
        { timestamp: 15, text: "Mediterranean diet basics: lots of vegetables, fruits, and whole grains." },
        { timestamp: 22, text: "Omega-3 fatty acids are essential - eat fish twice weekly." },
        { timestamp: 29, text: "Salmon, sardines, or mackerel are excellent choices." },
        { timestamp: 36, text: "Berries contain antioxidants - aim for a cup daily." },
        { timestamp: 43, text: "Blueberries, strawberries, or blackberries all work well." },
        { timestamp: 50, text: "Stay hydrated: 8 glasses of water throughout the day." },
        { timestamp: 57, text: "Set reminders to drink water - dehydration affects cognition." },
        { timestamp: 64, text: "Limit processed foods and excess sugar - they cause inflammation." },
        { timestamp: 71, text: "Meal timing: eat at regular intervals to stabilize blood sugar." },
        { timestamp: 78, text: "Take medications with food unless directed otherwise." },
        { timestamp: 85, text: "Green leafy vegetables daily - spinach, kale, or collards." },
        { timestamp: 92, text: "Nuts and seeds provide healthy fats - a handful daily." },
        { timestamp: 99, text: "Prepare simple meals in advance when you're feeling clear." },
        { timestamp: 106, text: "Keep a food diary to track what works best for you." }
      ]
    }
  ];

  // Reset messages when opening a new diagnostic
  useEffect(() => {
    if (selectedDiagnostic) {
      setMessages([
        {
          id: 1,
          text: "Hello! I'm here to help you understand your diagnostic results. Pause the video at any time to ask questions about what you've watched so far.",
          sender: 'bot',
          timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
        }
      ]);
      setCurrentTime(0);
      setIsPlaying(false);
    }
  }, [selectedDiagnostic]);

  // Scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // Handle simulated video playback
  useEffect(() => {
    // Clear any existing interval
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }

    if (selectedDiagnostic) {
      // Set duration to 120 seconds for all simulated videos
      setDuration(120);

      if (isPlaying) {
        intervalRef.current = setInterval(() => {
          setCurrentTime(prev => {
            const newTime = prev + 1; // Increment by 1 second
            if (newTime >= 120) {
              setIsPlaying(false); // Stop at the end
              return 120;
            }
            return newTime;
          });
        }, 1000); // Update every second
      }
    }

    // Cleanup function
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    };
  }, [selectedDiagnostic, isPlaying]);

  const handleSendMessage = async () => {
    if (inputMessage.trim() && selectedDiagnostic) {
      const newMessage: Message = {
        id: messages.length + 1,
        text: inputMessage,
        sender: 'user',
        timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
      };
      setMessages([...messages, newMessage]);
      setInputMessage('');
      setIsLoading(true);

      try {
        // Debug: log what we're sending
        console.log('Sending to API:', {
          message: inputMessage,
          currentTime: currentTime,
          videoDuration: duration || 120,
          transcriptLength: selectedDiagnostic.transcript?.length,
          relevantTranscripts: selectedDiagnostic.transcript?.filter(item => item.timestamp <= currentTime).length
        });

        // Call the Python backend API
        const response = await fetch('http://localhost:8000/api/chat/', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            message: inputMessage,
            transcript: selectedDiagnostic.transcript || [],
            current_time: currentTime,
            video_duration: duration || 120 // Updated fallback duration
          }),
        });

        const data = await response.json();

        const botResponse: Message = {
          id: messages.length + 2,
          text: data.response || "I apologize, I couldn't process your question. Please try again.",
          sender: 'bot',
          timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
        };
        setMessages(prev => [...prev, botResponse]);
      } catch (error) {
        console.error('Error sending message:', error);
        const errorResponse: Message = {
          id: messages.length + 2,
          text: "I'm having trouble connecting to the backend service. Please make sure the Python backend is running on port 8000.",
          sender: 'bot',
          timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
        };
        setMessages(prev => [...prev, errorResponse]);
      } finally {
        setIsLoading(false);
      }
    }
  };

  const togglePlayPause = () => {
    setIsPlaying(!isPlaying);

    // Show a helpful message when paused for the first time
    if (isPlaying && messages.length === 1) {
      const pauseMessage: Message = {
        id: messages.length + 1,
        text: "Video paused. Feel free to ask questions about what you've watched so far! I can help explain any instructions or clarify anything that's unclear.",
        sender: 'bot',
        timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
      };
      setMessages(prev => [...prev, pauseMessage]);
    }
  };

  const resetVideo = () => {
    setCurrentTime(0);
    setIsPlaying(false);
    if (videoRef.current) {
      videoRef.current.currentTime = 0;
      videoRef.current.pause();
    }
  };

  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed':
        return 'default';
      case 'pending':
        return 'secondary';
      case 'in-progress':
        return 'outline';
      default:
        return 'default';
    }
  };

  return (
    <DashboardLayout userType="patient" userName="John Doe">
      <div className="space-y-6">
        {/* Header */}
        <div>
          <h1 className="text-3xl font-bold">Diagnostics History</h1>
          <p className="text-muted-foreground mt-2">View and review your past diagnostic sessions</p>
        </div>

        {/* Diagnostics List */}
        <Card title="All Diagnostics" subtitle="Click on any date to view details">
          <div className="space-y-4">
            {diagnosticHistory.map((diagnostic) => (
              <button
                key={diagnostic.id}
                onClick={() => setSelectedDiagnostic(diagnostic)}
                className="w-full text-left transition-colors hover:bg-accent rounded-lg"
              >
                <div className="flex items-start justify-between p-4 border rounded-lg">
                  <div className="flex-1">
                    <div className="flex items-center space-x-3">
                      <Calendar className="w-4 h-4 text-muted-foreground" />
                      <p className="font-medium">{diagnostic.date}</p>
                      <Badge variant={getStatusColor(diagnostic.status)}>
                        {diagnostic.status}
                      </Badge>
                    </div>
                    <div className="mt-2 ml-7">
                      <p className="text-sm font-medium">{diagnostic.type}</p>
                      <p className="text-sm text-muted-foreground mt-1">{diagnostic.summary}</p>
                      <div className="flex items-center mt-2 space-x-4">
                        <span className="text-xs text-muted-foreground flex items-center">
                          <Clock className="w-3 h-3 mr-1" />
                          {diagnostic.time}
                        </span>
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center">
                    <Video className="w-5 h-5 text-muted-foreground" />
                  </div>
                </div>
              </button>
            ))}
          </div>
        </Card>
      </div>

      {/* Modal for viewing diagnostic details */}
      <Dialog open={!!selectedDiagnostic} onOpenChange={() => setSelectedDiagnostic(null)}>
        <DialogContent className="max-w-[95vw] w-full h-[90vh]">
          <DialogHeader>
            <DialogTitle>
              {selectedDiagnostic?.type} - {selectedDiagnostic?.date}
            </DialogTitle>
          </DialogHeader>

          <div className="flex gap-4 h-[calc(90vh-8rem)]">
            {/* Left side - Video with controls */}
            <div className="w-[70%] flex flex-col">
              <div className="flex-1 bg-gradient-to-br from-blue-900 to-purple-900 rounded-lg relative overflow-hidden flex items-center justify-center">
                {/* Simulated Video Display */}
                <div className="text-white text-center p-8">
                  <Video className="w-20 h-20 mx-auto mb-4 opacity-50" />
                  <h3 className="text-xl font-semibold mb-2">{selectedDiagnostic?.type}</h3>
                  <p className="text-sm opacity-75 mb-4">Medical Instruction Video</p>

                  {/* Display current time like a video player */}
                  <div className="text-3xl font-mono mb-4">
                    {formatTime(currentTime)}
                  </div>

                  <p className="text-xs opacity-50">
                    {isPlaying ? '▶ Playing' : '⏸ Paused'}
                  </p>
                </div>

                {/* Subtitle Display - Always visible */}
                {selectedDiagnostic?.transcript && (
                  <div className="absolute bottom-4 left-4 right-4 bg-black/90 text-white p-4 rounded-lg">
                    <p className="text-lg text-center leading-relaxed">
                      {(() => {
                        const currentSubtitle = selectedDiagnostic.transcript
                          .filter(item => item.timestamp <= currentTime)
                          .slice(-1)[0];
                        return currentSubtitle ? currentSubtitle.text :
                          "Click play to start watching the medical instructions";
                      })()}
                    </p>
                    <p className="text-xs text-center mt-2 opacity-60">
                      {formatTime(currentTime)} / {formatTime(duration)}
                    </p>
                  </div>
                )}
              </div>

              {/* Video Controls */}
              <div className="bg-card border rounded-lg p-4 mt-2">
                <div className="flex items-center justify-between mb-2">
                  <div className="flex items-center space-x-2">
                    <Button
                      onClick={togglePlayPause}
                      size="sm"
                      variant="outline"
                    >
                      {isPlaying ? <Pause className="w-4 h-4" /> : <Play className="w-4 h-4" />}
                    </Button>
                    <Button
                      onClick={resetVideo}
                      size="sm"
                      variant="outline"
                    >
                      <RotateCw className="w-4 h-4" />
                    </Button>
                    <span className="text-sm text-muted-foreground">
                      {formatTime(currentTime)} / {formatTime(duration || 0)}
                    </span>
                  </div>
                  <div className="text-sm text-muted-foreground">
                    {isPlaying ? 'Playing' : 'Paused'} • Ask questions about what you've watched
                  </div>
                </div>

                {/* Progress Bar with manual control */}
                <input
                  type="range"
                  min="0"
                  max={duration || 120}
                  value={currentTime}
                  onChange={(e) => {
                    const newTime = parseFloat(e.target.value);
                    setCurrentTime(newTime);
                    if (videoRef.current) {
                      videoRef.current.currentTime = newTime;
                    }
                  }}
                  className="w-full h-2 bg-secondary rounded-lg appearance-none cursor-pointer slider"
                  style={{
                    background: `linear-gradient(to right, hsl(var(--primary)) 0%, hsl(var(--primary)) ${duration ? (currentTime / duration) * 100 : 0}%, hsl(var(--secondary)) ${duration ? (currentTime / duration) * 100 : 0}%, hsl(var(--secondary)) 100%)`
                  }}
                />
              </div>
            </div>

            {/* Right side - Chat */}
            <div className="w-[30%] flex flex-col border rounded-lg">
              <div className="p-4 border-b">
                <div className="flex items-center space-x-2">
                  <MessageSquare className="w-5 h-5" />
                  <h3 className="font-semibold">AI Assistant</h3>
                </div>
                <p className="text-xs text-muted-foreground mt-1">
                  Ask questions about your diagnostic results
                </p>
              </div>

              <ScrollArea className="flex-1 p-4">
                <div className="space-y-4">
                  {messages.map((message) => (
                    <div
                      key={message.id}
                      className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}
                    >
                      <div
                        className={`max-w-[80%] p-3 rounded-lg ${
                          message.sender === 'user'
                            ? 'bg-primary text-primary-foreground'
                            : 'bg-muted'
                        }`}
                      >
                        <p className="text-sm">{message.text}</p>
                        <p className="text-xs mt-1 opacity-70">{message.timestamp}</p>
                      </div>
                    </div>
                  ))}
                  <div ref={messagesEndRef} />
                </div>
              </ScrollArea>

              <div className="p-4 border-t">
                <div className="flex space-x-2">
                  <Input
                    placeholder="Type your message..."
                    value={inputMessage}
                    onChange={(e) => setInputMessage(e.target.value)}
                    onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
                  />
                  <Button onClick={handleSendMessage} size="icon" disabled={isLoading}>
                    <Send className="w-4 h-4" />
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </DashboardLayout>
  );
}